import Vue from 'vue'
import Vuex from 'vuex'

import { getLocal, setLocal, delSession } from '../assets/js/storage'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    // 登录状态
    isLogin: false,
    token: ''
  },
  mutations: {
    setToken(state, token) {
      state.token = token
      state.isLogin = true
      setLocal('ease_token', token)
    },
    delToken(state) {
      state.token = ''
      state.isLogin = false
      delSession('ease_token')
    }
  },
  actions: {},
  modules: {}
})
